import { Modal, Tag } from 'antd';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { gameUpdateAction } from 'redux/action/game.action';
import { State } from 'types/state.interface';
import playerBlue from 'assets/images/player-blue.svg';
import playerRed from 'assets/images/player-red.svg';
import door from 'assets/images/door.svg';
import exit from 'assets/images/exit.svg';

export const Spectate: React.FC<{}> = () => {
    const client = useSelector((state: State) => state.socket.client);
    const { room, x, o, map, turn } = useSelector((state: State) => state.game);
    const [lastMove, setLastMove] = useState<any>();
    const [winner, setWinner] = useState<'x' | 'o' | null>();
    const [winPattern, setWinPattern] = useState<string[]>([]);
    const [endgame, setEndgame] = useState(false);

    const dispatch = useDispatch();

    const navigate = useNavigate();

    useEffect(() => {
        if (!client) {
            return navigate('/');
        }

        client.on('player_join', (data: any) => {
            const { x, o, turn, map } = data;
            dispatch(gameUpdateAction({ x, o, turn, map }));
        });

        client.on('player_leave', (data: any) => {
            const { x, o, turn, map } = data;
            dispatch(gameUpdateAction({ x, o, turn, map }));
        });

        client.on('turn', (data: any) => {
            const { x, o, turn, map, lastMove } = data;
            dispatch(gameUpdateAction({ x, o, turn, map }));
            setLastMove(lastMove);
        });

        client.on('endgame', (data: any) => {
            setWinner(data.winner);
            setWinPattern(data.winPattern);
            setEndgame(true);
        });

        return () => {
            client.off('player_join');
            client.off('player_leave');
            client.off('turn');
            client.off('endgame');
        };
    }, []);

    return (
        <div className="main">
            <div className="room">
                <div className="room-info">
                    <img src={door} alt="" />
                    <div className="room-number">{room}</div>
                    <img src={exit} onClick={() => navigate('/')} alt="" />
                </div>
            </div>
            <div className="game">
                <div className="player player-x">
                    <div className="avatar">
                        <img src={playerBlue} alt="" />
                    </div>
                    <div className="info">
                        <div className="name">{x?.name || 'Waiting for player'}</div>
                        <div className="turn">{turn === 'x' && <Tag color="#43F43F">player turn</Tag>}</div>
                    </div>
                </div>

                <div className="player player-o">
                    <div className="avatar">
                        <img src={playerRed} alt="" />
                    </div>
                    <div className="info">
                        <div className="name">{o?.name || 'Waiting for player'}</div>
                        <div className="turn">{turn === 'o' && <Tag color="#43F43F">player turn</Tag>}</div>
                    </div>
                </div>

                <div className="board-container">
                    <div className="board">
                        {map?.map((row, r) => (
                            <div className="row" key={r}>
                                {row.map((cell, c) => {
                                    let cellClassName = '';
                                    if (lastMove?.row === r && lastMove?.col === c) {
                                        cellClassName = 'last-move';
                                    }
                                    if (winPattern.includes(r + '-' + c)) {
                                        cellClassName = String(winner) + '-win';
                                    }
                                    switch (cell) {
                                        case 'x': {
                                            return (
                                                <div className={cellClassName} key={c}>
                                                    <div className="cell x"></div>
                                                </div>
                                            );
                                        }
                                        case 'o': {
                                            return (
                                                <div className={cellClassName} key={c}>
                                                    <div className="cell o"></div>
                                                </div>
                                            );
                                        }
                                        default: {
                                            return (
                                                <div className={cellClassName} key={c}>
                                                    <div className="cell blank"></div>
                                                </div>
                                            );
                                        }
                                    }
                                })}
                            </div>
                        ))}
                    </div>
                </div>

                <Modal
                    className="endgame"
                    open={endgame}
                    title="We have a winner"
                    cancelText="Stay"
                    okText="Leave"
                    onCancel={() => setEndgame(false)}
                    onOk={() => navigate('/')}
                >
                    <div className={winner === 'x' ? 'win' : 'lose'}>{winner === 'x' ? x?.name : o?.name} win</div>
                </Modal>
            </div>

            <div className="cell x o" style={{ display: 'none' }} />
        </div>
    );
};
