import { GameState } from 'types/game.interface';

export const gameActions = {
    UPDATE: 'game-update',
    CLEAR: 'game-clear',
};

export const gameUpdateAction = (payload: GameState) => {
    return {
        type: gameActions.UPDATE,
        payload: payload,
    };
};

export const gameClearAction = () => {
    return {
        type: gameActions.CLEAR,
    };
};
