import { SocketState } from 'types/socket.interface';

export const socketActions = {
    CONNECT: 'socket-connect',
    DISCONNECT: 'socket-disconnect ',
};

export const socketConnectAction = (payload: SocketState) => {
    return {
        type: socketActions.CONNECT,
        payload,
    };
};

export const socketDisconnectAction = () => {
    return {
        type: socketActions.DISCONNECT,
    };
};
