import { SocketAction, SocketState } from 'types/socket.interface';
import { socketActions } from '../action/socket.action';

const defaultState: SocketState = {};

export const socketReducer = (state = defaultState, action: SocketAction) => {
    switch (action.type) {
        case socketActions.CONNECT: {
            if (state.client?.connected) {
                state.client.disconnect();
            }
            return action.payload;
        }
        case socketActions.DISCONNECT: {
            if (state.client?.connected) {
                state.client.disconnect();
            }
            return {};
        }
        default: {
            return state;
        }
    }
};
