import { Button, Form, Input, InputNumber, message, Modal } from 'antd';
import logo from 'assets/images/logo.png';
import { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { gameClearAction, gameUpdateAction } from 'redux/action/game.action';
import { socketConnectAction, socketDisconnectAction } from 'redux/action/socket.action';
import { io } from 'socket.io-client';
import { CallbackError } from 'types/callback-error.interface';

export const Home: React.FC<{}> = () => {
    const [modalOpen, setModalOpen] = useState<string>();

    const dispatch = useDispatch();

    const [formRandom] = Form.useForm();
    const [formCreate] = Form.useForm();
    const [formJoin] = Form.useForm();
    const [formSpectate] = Form.useForm();

    const navigate = useNavigate();

    useEffect(() => {
        dispatch(socketDisconnectAction());
        dispatch(gameClearAction());
    }, []);

    const closeModal = () => {
        setModalOpen(undefined);
    };

    const handleJoinRoom = (values: any, joinEvent: string, path = '/play') => {
        const client = io(process.env.SERVER_URL + '/pvp', { transports: ['websocket'] });

        if (values.name) localStorage.setItem('name', values.name);

        client.emit(joinEvent, values, (err: CallbackError, data: any) => {
            if (err) {
                return message.error(err.message);
            }

            const { room, you } = data;
            dispatch(gameUpdateAction({ room, you }));

            navigate(path);
        });

        client.on('player_join', (data: any) => {
            const { x, o, turn, map } = data;
            dispatch(gameUpdateAction({ x, o, turn, map }));
        });

        client.on('player_leave', (data: any) => {
            const { x, o, turn, map } = data;
            dispatch(gameUpdateAction({ x, o, turn, map }));
        });

        client.on('turn', (data: any) => {
            const { x, o, turn, map } = data;
            dispatch(gameUpdateAction({ x, o, turn, map }));
        });

        dispatch(socketConnectAction({ client }));
    };

    const handleRandom = async () => {
        try {
            const values = await formRandom.validateFields();
            handleJoinRoom(values, RANDOM);
        } catch (err: any) {
            return;
        }
    };

    const handleCreate = async () => {
        try {
            const values = await formCreate.validateFields();
            handleJoinRoom(values, CREATE);
        } catch (err: any) {
            return;
        }
    };

    const handleJoin = async () => {
        try {
            const values = await formJoin.validateFields();
            handleJoinRoom(values, JOIN);
        } catch (err: any) {
            return;
        }
    };

    const handleSpectate = async () => {
        try {
            const values = await formSpectate.validateFields();
            handleJoinRoom(values, SPECTATE, '/spectate');
        } catch (err: any) {
            return;
        }
    };

    return (
        <div className="home">
            <div className="welcome mb-24">Welcome to</div>

            <div className="left-buttons buttons">
                <Button type="primary" onClick={() => setModalOpen(RANDOM)}>
                    PLAY ONLINE
                </Button>
                <Button type="primary" onClick={() => setModalOpen(CREATE)}>
                    CREATE ROOM
                </Button>
            </div>

            <div className="logo mb-24">
                <img src={logo} alt="" />
            </div>

            <div className="right-buttons buttons">
                <Button type="primary" onClick={() => setModalOpen(JOIN)}>
                    JOIN ROOM
                </Button>
                <Button type="primary" onClick={() => setModalOpen(SPECTATE)}>
                    SPECTATE
                </Button>
            </div>

            <Modal title="Play online" open={modalOpen === RANDOM} onCancel={closeModal} onOk={handleRandom}>
                <Form form={formRandom} initialValues={{ name: localStorage.getItem('name') }}>
                    <Form.Item label="Name" name="name" rules={[{ required: true, message: 'Name is required' }]}>
                        <Input placeholder="Type your name here" />
                    </Form.Item>
                </Form>
            </Modal>

            <Modal title="Create room" open={modalOpen === CREATE} onCancel={closeModal} onOk={handleCreate}>
                <Form form={formCreate} initialValues={{ name: localStorage.getItem('name') }}>
                    <Form.Item label="Name" name="name" rules={[{ required: true, message: 'Name is required' }]}>
                        <Input placeholder="Type your name here" />
                    </Form.Item>
                </Form>
            </Modal>

            <Modal title="Join room" open={modalOpen === JOIN} onCancel={closeModal} onOk={handleJoin}>
                <Form form={formJoin} initialValues={{ name: localStorage.getItem('name') }}>
                    <Form.Item label="Name" name="name" rules={[{ required: true, message: 'Name is required' }]}>
                        <Input placeholder="Type your name here" />
                    </Form.Item>
                    <Form.Item label="Room" name="room" rules={[{ required: true, message: 'Room ID is required' }]}>
                        <InputNumber type="number" controls={false} placeholder="Type room ID here" />
                    </Form.Item>
                </Form>
            </Modal>

            <Modal title="Spectate" open={modalOpen === SPECTATE} onCancel={closeModal} onOk={handleSpectate}>
                <Form form={formSpectate} initialValues={{ name: localStorage.getItem('name') }}>
                    <Form.Item label="Room" name="room" rules={[{ required: true, message: 'Room ID is required' }]}>
                        <InputNumber type="number" controls={false} placeholder="Type room ID here" />
                    </Form.Item>
                </Form>
            </Modal>
        </div>
    );
};

const RANDOM = 'join_random';
const CREATE = 'create';
const JOIN = 'join';
const SPECTATE = 'spectate';
