import { gameActions } from 'redux/action/game.action';
import { GameAction, GameState } from 'types/game.interface';

const defaultState: GameState = {};

export const gameReducer = (state = defaultState, action: GameAction) => {
    switch (action.type) {
        case gameActions.UPDATE: {
            return {
                ...state,
                ...action.payload,
            };
        }
        case gameActions.CLEAR: {
            return {};
        }
        default: {
            return state;
        }
    }
};
